import { useState } from "react";
import "./UserWriteTodo.css";

//<UserWriteTodo placeholder=" 1" />
//  : writetodo 
//App.js const [userTodo, setUserTodo] = useState([]);  

function UserWriteTodo({ placeholder, setUserTodo }) {
  const [writeTodo, setWriteTodo] = useState("");
  const onChange = (e) => setWriteTodo(e.target.value);
  const onSubmit = (e) => {
    e.preventDefault();
    if (writeTodo === "") {
      return;
    }
    setWriteTodo((currentList) => currentList);
    setUserTodo((currentTodo) => [...currentTodo, writeTodo]);
  };

  //  
  return (
    <div>
      <form className="category-form" onSubmit={onSubmit}>
        <input
          value={writeTodo}
          onChange={onChange}
          type="text"
          placeholder={placeholder}
          className="category-input"
        />
      </form>
    </div>
  );
}

export default UserWriteTodo;
