import React, { useState, useEffect } from "react";
import "./Category.css";

//`"todoList"${number}`

function Category({ number }) {
  // 
  const [category, setCategory] = useState(
    JSON.parse(localStorage.getItem(`"categoryList"${number}`)) || ""
  );

  //   Local Storage 
  useEffect(() => {
    localStorage.setItem(`"categoryList"${number}`, JSON.stringify(category));
  }, [category]);
  // local storage      
  useEffect(() => {
    const storedCategory = JSON.parse(
      localStorage.getItem(`"categoryList"${number}`)
    );
    if (storedCategory) {
      setCategory(storedCategory);
    }
  }, []);

  // onchange, onsubmit 
  const onChangeCategory = (event) => setCategory(event.target.value);
  const onSubmitCate = (event) => {
    event.preventDefault();
    if (category == "") {
      return;
    }
    setCategory(category);
  };
  return (
    <form onSubmit={onSubmitCate} className="category-form">
      <input
        onChange={onChangeCategory}
        value={category}
        type="text"
        className="todo-category"
      />
    </form>
  );
}

export default Category;
