import "./PhoneDropDown.css";

export const PhoneDropDown = ({
  value,
  setPhoneIdentify,
  setIsOpen,
  isOpen,
}) => {
  const ValueClick = () => {
    setPhoneIdentify(value);
    setIsOpen(!isOpen);
  };
  return (
    <li className="timetable-li" onClick={ValueClick}>
      {value}
    </li>
  );
};
